.order-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #8a2b06;
  padding: 1rem 0;
  margin: 1rem 0;
}

.order-item h3 {
  margin: 0 0 0.5rem 0;
  color: #363636;
}

.summary {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-self: end;
}

.amount {
  font-weight: bold;
  border: 1px solid #ccc;
  padding: 0.25rem 0.75rem;
  border-radius: 6px;
  color: #363636;
}
